* {box-sizing: border-box;}
#root {overflow: hidden;}
html {height:100%;overflow-y:scroll; -ms-text-size-adjust:100%; -webkit-text-size-adjust:100%;}
body {height:100%;background:transparent;-webkit-font-smoothing:antialiased; -webkit-overflow-scrolling:touch;font-size:12px;color:#444;}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,textarea,p,blockquote,th,td,input,select,textarea,button {margin:0; padding:0; letter-spacing: -0.3px}
body, h1, h2, h3, h4, h5, h6, input, textarea, select {font-family:'Noto Sans KR', 'sans-serif'; }
/*article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section {display:block;}*/
h1,h2,h3,h4,h5,h6 {font-weight:normal; font-size:100%}
input, select, textarea, button, label {font-size:100%; vertical-align:middle;}
select { color:#333; line-height:1.1; padding-left:5px; border:1px solid #8d8d8d;}
input {border:0;}
input[type="text"],
input[type="password"],
input[type="tel"], input[type="email"] {border:1px solid #8d8d8d;}
fieldset, img, iframe {border:0 none}
b, strong {font-weight:600}
img {vertical-align:middle;border-style:none}
textarea {width:100%;padding:10px;resize:none;border:1px solid #ccc;background:#f9f9f9;color:#666666;}
em, address {font-style:normal}
a {text-decoration:none; background-color:transparent; vertical-align:middle; -webkit-text-decoration-skip:objects; color:#2d2d2d; word-break: break-word}
a:hover, a:active, a:focus {text-decoration:none}
a:active {color:#333}
menu,li {list-style:none}
button {padding:0; margin:0; border:0; cursor:pointer; vertical-align:middle; background:none;}
button, input {overflow:visible}
button, select {text-transform:none}
button, html [type="button"], [type="reset"],[type="submit"] {-webkit-appearance:button}
button::-moz-focus-inner,[type="button"]::-moz-focus-inner,[type="reset"]::-moz-focus-inner,[type="submit"]::-moz-focus-inner {border-style:none;padding:0}
button:-moz-focusring,[type="button"]:-moz-focusring,[type="reset"]:-moz-focusring,[type="submit"]:-moz-focusring {outline:1px dotted ButtonText}
caption {width:0; height:0; font-size:0; line-height:0; overflow:hidden; /* visibility:hidden */}
hr {display:none}
i,em,address{font-style:normal}
label {cursor:pointer}
table {border-spacing:0; border-collapse:collapse; color:#2d2d2d;}
legend {position:absolute;top:0;left:-1000000000px;height:0;font-size:0;}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color:#666;
}
::-moz-placeholder { /* Firefox 19+ */
    color:#666;
}
:-ms-input-placeholder { /* IE 10+ */
    color:#666;
}
:-moz-placeholder { /* Firefox 18- */
    color:#666;
}
.sr-only {
    position: absolute;
    overflow: hidden;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    border: 0;
    clip: rect(0,0,0,0);
}

::-webkit-scrollbar {
    width: 5px;
}
::-webkit-scrollbar-thumb {
    background-color: rgba(0,0,0,0.4);
    border-radius: 0px;
}
::-webkit-scrollbar-track {
    background-color: transparent;
}

.form_tip {font-size:12px; color:#FF0000; margin-top: 10px;}
.form_tip:before {content:'※'; border-radius: 50%; margin-right:4px;}

.close_menu {display: none;}

