.header_wrap {position: relative; z-index: 9; max-width:1440px; margin:0 auto; padding:34px 20px; display: flex; justify-content: space-between; align-items: center;}
.header {position: fixed; width:100%;  left:50%; transform: translateX(-50%); z-index: 999 !important;}
.header.on {background:rgba(0,0,0,0.5);}
.header.on:after {content:''; display: block; width: 100%; height:100%; background:hsla(0,0%,100%,.1); position: absolute; top:0; left:0; backdrop-filter: blur(15px);}
.header .nav { display: flex; justify-content: space-between; flex: 1; padding:0 110px; }
.header .nav a {color:#fff; font-size:18px; font-weight: 500; position: relative; transition: 0.5s ease-in-out;}
.header .nav a.active:after {content: ''; position: absolute; bottom:-10px; left:50%; transform: translateX(-50%); display: inline-block; height:3px; width:100%; background:#FF0000; box-shadow: 0px 3px 6px #FF0000;}
.header .lang button {color:#707070; font-size:16px; font-weight: 500;}
.header .lang button.on {color:#fff;}
.header .lang button + button:before {content:''; display: inline-block; width: 1px; height:11px; background:#707070; margin:0 15px; }
.header_wrap img {transition: 0.3s ease-in-out;}
.header_wrap img:hover {filter: brightness(120%)}
.header_wrap > * {flex:auto;}
.header_wrap > *.mo_box {flex:100;}
.header_wrap .mo {display: flex; align-items: center;}


