.cryptocurrency li:hover:nth-of-type(4) {padding-top: 60px;}
.cryptocurrency li:nth-of-type(5) {border-color:#735AD5; bottom:53%; right:19%;}
.cryptocurrency li:hover:nth-of-type(5) {bottom:48%;}
