.chk_box input + label {margin-left:14px; display: inline-block; vertical-align: middle; line-height: 1;}

.form_box {max-width: 1440px; padding:0 20px; margin:0 auto;}
.form_box .btn1 {margin:40px auto 0; display: block;}
.form_box * {outline: none;}
.form_box *:focus {outline: 2px solid #d2b89d; border-radius: 30px;}
.form_box .flex {display: flex; align-items:start;}
.form_box .flex > .input_box {flex:1;}
.form_box .flex > .input_box + .input_box {margin-left:30px;}
.form_box input {padding:25px 27px 21px; border:none; background:#fff; width:100%; border-radius: 30px; font-size:20px; line-height:29px; color:#636363;}
.form_box input::placeholder {color:#939599;}
.form_box textarea {min-height:500px; margin-top:30px; vertical-align: top; padding:25px 27px 21px; border:none; background:#fff; width:100%; border-radius: 30px; font-size:20px; line-height:29px; color:#636363;}
.form_box textarea::placeholder {color:#939599;}
.brightness {
    animation-name: brightness;
    animation-iteration-count: infinite;
    animation-duration:5s;
    animation-timing-function:ease-in-out;
}

@keyframes brightness {
    0% {filter: brightness(90%);}
    30% {filter: brightness(115%);}
    60% {filter: brightness(90%);}
    80% {filter: brightness(120%);}
    100% {filter: brightness(90%);}
}

